const appAnimation = {
  In: {
    animation: 'transition.bounceRightIn',
    delay: 0,
    duration: 100,
    // stagger: '150',
    display: 'flex',
  },
  Out: {
    animation: 'transition.slideRightOut', 
    // delay: 500,
    duration: 200
  }
};

export default appAnimation;


